/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.tabsBox {
  background-color: #f4f5f7;
  display: flex;
  flex-direction: column;
}

.contentWrapper {
  display: flex;
}

.tabsContainer {
  display: flex;
  border: 5px solid transparent;
  border-top: none;
  gap: 4px;
}

.tabButton {
  position: relative;
  border: none;
  border-top: 4px solid #f4f5f7;
  background-color: white;
  width: 100%;
  padding: 0.75rem;
  cursor: pointer;
}
.tabButton:focus {
  outline: none;
}
.tabButton:hover {
  background-color: #f4f5f7;
}
.tabButton.active {
  background-color: #f4f5f7;
}
.tabButton.active h4 {
  color: #d58e32;
}
.tabButton.active::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -4px;
  display: block;
  height: 4px;
  background-color: #d58e32;
  width: calc(100% + 8px);
}
.tabButton h4 {
  color: rgba(42, 42, 42, 0.75);
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  margin: 0;
  letter-spacing: 1px;
  font-weight: 700;
}