/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  box-shadow: 2px 3.464px 6px rgba(1, 2, 2, 0.2);
  background-color: #ffffff;
}
.root :global(.container) > :global(.row) {
  min-height: 84px;
}
.root .menu {
  align-self: stretch;
}
.root .menu ul {
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}
.root .menu ul li {
  list-style: none;
  display: flex;
  align-items: stretch;
}
.root .menu a {
  color: #2a2a2a;
  text-transform: uppercase;
  font-size: 12px;
  padding: 1rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  border-top: 4px solid #ffffff;
  font-weight: 500;
  letter-spacing: 1px;
}
.root .menu a:hover, .root .menu a.active {
  background-color: #d58e32;
  color: #ffffff;
  border-color: #d58e32;
}
.root .menu a.active {
  border-color: #2a2a2a;
}
.root .dropdown a {
  width: 100%;
}