/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  margin-bottom: 2rem;
}
.root .photo {
  position: relative;
  background-color: #f4f5f7;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  align-items: flex-end;
}
.root .photo img {
  object-fit: cover;
  width: 100%;
  height: 300px;
  padding: 0;
}
.root .photo .button {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.root .photo .counter {
  position: absolute;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  bottom: 0;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.root .photo .counter .amount {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  line-height: 1.1;
  word-spacing: 15px;
  background-color: rgba(41, 41, 41, 0.8);
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.root .photo:hover .button {
  display: block;
}
.root .content {
  text-align: center;
  padding: 20px;
}
.root .content h5 {
  color: #2a2a2a;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 1px;
}
.root .content .stars a {
  text-decoration: none;
  color: rgb(42, 42, 42);
}
.root .line {
  margin: 0 10px;
  border-bottom: 1px solid #2a2a2a;
  position: relative;
}
.root .line::before {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 50%;
  width: 30px;
  border-bottom: 4px solid #d58e32;
  transform: translateX(-50%);
}
.root .actions {
  padding: 15px 10px 10px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}