/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

@keyframes fade-in {
  0% {
    filter: opacity(0);
  }
  100% {
    filter: opacity(1);
  }
}
@keyframes fade-out {
  0% {
    filter: opacity(1);
  }
  100% {
    filter: opacity(0);
  }
}
@keyframes scale-in {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
.root {
  height: 500px;
  width: 100%;
}
.root .dealInfo {
  position: relative;
  height: calc(100% - 35px);
}
.root .dealInfo img {
  height: 100%;
  width: 100%;
  object-fit: fill;
}
.root .dealInfo .infoBg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 35%;
  background-color: rgba(66, 66, 66, 0.6);
}
.root .dealInfo .infoBg .infoTexts {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  color: #ffffff;
}
.root .dealInfo .infoBg .infoTexts .mainText {
  font-size: 36px;
  margin: 0;
}
.root .dealInfo .infoBg .infoTexts .additionalText {
  color: rgba(255, 255, 255, 0.8);
  margin: 0;
}
.root .dealInfo .infoBg .button {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  display: inline-block;
  background-color: #ffffff;
  padding: 5px 15px;
  border-radius: 2px;
}
.root .dealInfo .infoBg .button a {
  color: #2a2a2a;
  text-transform: uppercase;
  font-weight: bold;
}
.root .dealInfo .infoBg .button:hover a {
  color: #ffffff;
  text-decoration: none;
}
.root .dealInfo .infoBg .button:hover {
  background-color: #2a2a2a;
  cursor: pointer;
}
.root .buttons {
  display: flex;
  height: 35px;
  width: 100%;
}
.root .buttons a {
  display: block;
  text-align: center;
  width: 50%;
  font-size: 16px;
}
.root .fadeOut {
  opacity: 0;
  transition: opacity 700ms;
}
.root .fadeIn {
  animation: fade-in 700ms;
}