/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 1fr;
  gap: 10px;
}

.leftColumn {
  aspect-ratio: 1/1;
}

.rightColumn {
  background-color: #f4f5f7;
}
.rightColumn .rightImage {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.mobileLayout {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 1fr;
  gap: 10px;
}

.rightColumnBlock {
  display: none;
}