/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

@keyframes fade-in {
  0% {
    filter: opacity(0);
  }
  100% {
    filter: opacity(1);
  }
}
@keyframes fade-out {
  0% {
    filter: opacity(1);
  }
  100% {
    filter: opacity(0);
  }
}
@keyframes scale-in {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
.root {
  margin-top: 10px;
  height: 500px;
}
.root .hotDeal {
  height: 500px;
}
.root .dealBg {
  background-color: #363636;
  display: flex;
  justify-content: space-between;
}
.root .dealBg .title {
  display: flex;
  text-transform: uppercase;
  color: #ffffff;
  margin-left: 10px;
  margin: auto 10px;
  font-size: 16px;
}
.root .dots {
  display: inline-block;
}
.root .dots ul {
  padding: 0 0 0 1rem;
  list-style: none;
}
.root .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .dots ul li a {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .dots ul li a.active, .root .dots ul li a:hover {
  background-color: #d58e32;
}
.root .fadeOut {
  opacity: 0;
}
.root .fadeIn {
  animation: fade-in 250ms;
}

:export {
  animationTime: 250ms;
  swapTime: 5000ms;
}