/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  background-color: #363636;
}
.root :global(.container) > :global(.row) {
  height: 145px;
}
.root .mobile {
  display: none;
}
.root .desktop {
  display: flex;
}
.root .phoneNumber p {
  margin: 0;
  font-size: 17px;
  line-height: 42px;
  color: rgb(216, 216, 216);
  font-weight: 500;
}
.root .phoneNumber p .icon {
  font-size: 32px;
  color: #d58e32;
  vertical-align: middle;
  margin-top: -3px;
  margin-right: 0.8rem;
}
.root .cart .cartBox {
  display: inline-block;
  position: relative;
  color: #ffffff;
}
.root .cart .cartBox .cartIcon {
  background-color: #d58e32;
  width: 55px;
  height: 50px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}
.root .cart .cartBox .cartIcon .icon {
  margin-right: 5px;
  color: white;
}
.root .cart .cartBox .cartCounter {
  width: 28px;
  height: 27px;
  border-radius: 14px;
  background-color: #363636;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: rgb(224, 227, 237);
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(50%, -50%);
}
.root .cart .cartBox:hover .cartIcon {
  background-color: #b07324;
}

@media (max-width: 576px) {
  .root .desktop {
    display: none;
  }
  .root .mobile {
    display: flex;
  }
  .root .logoBazar {
    flex: 0 0 40%;
  }
  .root .phoneNumber {
    padding: 0;
  }
  .root .phoneNumber p {
    font-size: 14px;
    padding: 0;
  }
  .root .phoneNumber p .icon {
    font-size: 24px;
    margin-right: 0.2rem;
  }
  .root .cart .cartBox .cartIcon {
    width: 44px;
    height: 40px;
    font-size: 20px;
    margin-bottom: 5px;
  }
}