/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  border: 1px solid #292929;
  display: inline-flex;
  height: 45px;
  color: #2a2a2a;
}
.root .category {
  border-right: 1px solid #292929;
  display: flex;
  align-items: center;
  position: relative;
}
.root .category .icon:first-child {
  color: #d58e32;
  position: absolute;
  left: 10px;
  z-index: 0;
}
.root .category .icon:last-child {
  position: absolute;
  right: 10px;
  z-index: 0;
}
.root .category .list {
  list-style: none;
  margin-bottom: 25px;
}
.root .category .list .mainItem {
  position: relative;
  font-size: 14px;
  top: 10px;
  width: 201px;
  z-index: 1;
}
.root .category .list .mainItem .childList {
  display: none;
  position: absolute;
  list-style: none;
  top: 100%;
  background-color: #fff;
  border: 1px solid #292929;
  border-top: 0;
  width: 242.7px;
  left: -41px;
}
.root .category .list .mainItem .childList.visible {
  display: block;
}
.root .category .list .mainItem .childList .childItem {
  padding: 5px 0;
  cursor: pointer;
}
.root .category .list .mainItem .childList .childItem:hover {
  background-color: #f0f0f0;
}
.root .category .list .mainItem:hover .childList {
  display: block;
}
.root .searchField {
  display: flex;
  align-items: center;
  position: relative;
}
.root .searchField input {
  height: 100%;
  border: 0;
  padding-left: 10px;
  font-size: 14px;
  outline: none;
}
.root .searchField input::placeholder {
  color: #2a2a2a;
}
.root .searchField button {
  border: 0;
  background-color: transparent;
  outline: none;
}