/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  padding: 30px 0;
}
.root .slider {
  height: 142px;
  margin: 40px auto;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border-top: 1px solid #b6b6b6;
  border-bottom: 1px solid #b6b6b6;
}
.root .slider .item {
  width: 144px;
  height: 93px;
  transition: transform 0.5s ease;
  margin: 10px;
  border: 1px solid #b6b6b6;
}
.root .slider .item img {
  width: 142px;
  height: 91px;
  object-fit: contain;
}
.root .slider button {
  width: 33px;
  height: 92px;
  margin-left: 10px;
  top: -10px;
  position: relative;
  background-color: #2a2a2a;
}
.root .slider button .fa {
  color: #ffffff;
}
.root .slider .dots {
  position: absolute;
  right: -10px;
  top: -50px;
}
.root .slider .dots ul {
  margin: 0;
  padding: 0 0 0 1rem;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(15px);
}
.root .slider .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .slider .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .slider .dots ul li a.active, .root .slider .dots ul li a:hover {
  background-color: #d58e32;
}