/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  width: 100vw;
}
.root h5 {
  margin-bottom: 10px;
}
.root .photo {
  width: 100%;
}
.root .name {
  margin: 20px 0;
  font-weight: bold;
}
.root .oldPrice {
  color: red;
}

.blur {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 999;
}