/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  padding: 30px 0;
}
.root .panelBar {
  margin-bottom: 30px;
  position: relative;
}
.root .panelBar :global(.row) > * {
  border-bottom: 4px solid #e2e2e2;
}
.root .panelBar .heading {
  position: relative;
  flex: 0 0 100%;
}
.root .panelBar .heading h3 {
  color: #d58e32;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
}
.root .panelBar .heading::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 45px;
  border-bottom: 4px solid #d58e32;
}
.root .dots {
  position: absolute;
  right: 0;
  top: 0;
}
.root .dots ul {
  margin: 0;
  padding: 0 0 0 1rem;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(15px);
}
.root .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .dots ul li a.active, .root .dots ul li a:hover {
  background-color: #d58e32;
}
.root .postsContainer {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.root .postsContainer .postImage {
  width: 100%;
  height: 260px;
  background-color: #434343;
}
.root .postsContainer .postInfo {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 290px;
  background-color: #ffffff;
}
.root .postsContainer .postInfo:last-of-type {
  align-items: flex-end;
}
.root .postsContainer .details {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 10px;
  padding: 20px 20px 25px 20px;
  color: #434343;
  width: 100%;
}
.root .postsContainer .content {
  width: 75%;
  margin: 0 auto;
}
.root .postsContainer .content .postText {
  color: #2a2a2a;
  font-size: 12px;
}
.root .postsContainer button {
  background-color: #2a2a2a;
  color: #ffffff;
  border: none;
  width: 150px;
  height: 40px;
  font-size: 12px;
  font-style: bold;
  transition: background-color 0.1s, #d58e32 0.1s;
}
.root .postsContainer .postInfo:hover button {
  background-color: #d58e32;
}
.root .postsContainer .postInfo:hover h6 {
  color: #d58e32;
}
.root .postsContainer .postInfo:hover .details,
.root .postsContainer .postInfo:hover .postText {
  color: rgb(106, 103, 103);
}