/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  padding: 30px 0;
}
.root .panelBar {
  margin-bottom: 30px;
  position: relative;
}
.root .panelBar :global(.row) > * {
  border-bottom: 4px solid #e2e2e2;
}
.root .panelBar .heading {
  position: relative;
  flex: 0 0 100%;
}
.root .panelBar .heading h3 {
  color: #d58e32;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
}
.root .panelBar .heading::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 45px;
  border-bottom: 4px solid #d58e32;
}
.root .dots {
  position: absolute;
  right: 10px;
  top: 0;
}
.root .dots ul {
  margin: 0;
  padding: 0 0 0 1rem;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(15px);
}
.root .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .dots ul li a.active, .root .dots ul li a:hover {
  background-color: #d58e32;
}
.root .quoteContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.root .quoteContainer .quoteIcon {
  font-size: 26px;
}
.root .quoteContainer .quoteTextBox {
  width: 70%;
  text-align: center;
}
.root .quoteContainer .quoteTextBox p {
  line-height: 1.8;
}
.root .quoteContainer .clientInfoBox {
  display: flex;
}
.root .quoteContainer .clientInfoBox img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  object-position: center;
  margin-right: 1.2rem;
}