/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.chatBox {
  position: fixed;
  left: 160px;
  bottom: 15%;
  width: 300px;
  background-color: transparent;
  z-index: 1;
  border: 1px solid #d58e32;
  max-height: 300px;
  border-radius: 5px;
}
.chatBox .top {
  background-color: #d58e32;
  border-bottom: 5px;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 40px;
  color: #ffffff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.chatBox .top .icon {
  padding-right: 10px;
  width: 50px;
  display: flex;
  justify-content: space-around;
  font-size: 14px;
}
.chatBox .textAreaView {
  background-color: #ffffff;
  border-top: 5px;
  padding: 5px;
  margin: 0;
  width: 100%;
  height: 250px;
  font-size: 14px;
  padding-left: 10px;
  overflow: auto;
}
.chatBox .textAreaView p {
  width: fit-content;
  max-width: 70%;
  padding: 2px 5px;
  border-radius: 10px;
  background-color: #c2c2c2;
}
.chatBox .textAreaView p:nth-child(odd) {
  color: #d58e32;
  margin-left: auto;
  background-color: rgba(213, 142, 50, 0.2117647059);
}
.chatBox .textArea {
  background-color: #ffffff;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 30px;
  color: #363636;
  bottom: 0px;
  display: flex;
  font-size: 14px;
  border: none;
}
.chatBox .textArea input {
  outline: none;
  font-size: 14px;
  width: 80%;
  height: 100%;
  padding: 0;
  margin: 0;
  color: #ffffff;
  background-color: #363636;
  border: none;
  padding-left: 10px;
}
.chatBox .textArea button {
  width: 20%;
  padding: 0;
  margin: 0;
  border: none;
  background-color: #363636;
}
.chatBox .textArea button svg {
  background-color: #363636;
  color: #ffffff;
  font-size: 14px;
}
.chatBox .bottom {
  background-color: #d58e32;
  border-bottom: 5px;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 40px;
  color: #ffffff;
  bottom: 0px;
}

.button {
  position: fixed;
  left: 20px;
  bottom: 30px;
  border: 1px solid #2a2a2a;
  border-radius: 50%;
  z-index: 1;
  cursor: pointer;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button a {
  color: #2a2a2a;
  font-style: normal;
  font-size: 14px;
  text-decoration: none;
}
.button .faEdit {
  font-size: 16px;
}

.text {
  left: 80px;
  bottom: 40px;
  color: #ffffff;
  position: fixed;
  background-color: #2a2a2a;
  opacity: 0.5;
  padding: 5px;
  border-radius: 5px;
  font-size: 14px;
}