@keyframes fade-in {
  0% {
    filter: opacity(0);
  }
  100% {
    filter: opacity(1);
  }
}


@keyframes fade-out {
  0% {
    filter: opacity(1);
  }
  100% {
    filter: opacity(0);
  }
}

@keyframes scale-in {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
