/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.wrapper {
  display: flex;
  width: 100%;
  padding: 0.5rem 0;
  height: 6rem;
  align-items: stretch;
  justify-content: space-between;
}

.button {
  width: 40px;
  height: 100%;
  border: none;
  background-color: black;
  color: white;
}
.button:focus {
  outline: none;
}

.thumbnailsWrapper {
  display: flex;
  gap: 5px;
  overflow: hidden;
  padding: 0 5px;
  position: relative;
}
.thumbnailsWrapper .thumbnail {
  position: relative;
  height: 100%;
  aspect-ratio: 1/1;
  overflow: hidden;
  flex-shrink: 0;
  opacity: 0.7;
  filter: brightness(0.7);
  cursor: pointer;
}
.thumbnailsWrapper .thumbnail.selected {
  opacity: 1;
  filter: brightness(1);
}
.thumbnailsWrapper .thumbnail img {
  height: 100%;
  object-fit: cover;
}