/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  background-color: #ffffff;
  border: 1px solid #e1e1e1;
  margin-bottom: 2rem;
}
.root .photo {
  position: relative;
  padding: 10px 10px 0 10px;
  background-color: #f4f5f7;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  align-items: flex-end;
}
.root .photo img {
  object-fit: cover;
  width: 100%;
  height: 185px;
  padding: 0;
}
.root .photo .sale {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #292929;
  border-radius: 50%;
  color: #ffffff;
  padding: 30px 15px 5px 15px;
}
.root .photo .buttons {
  display: flex;
  justify-content: space-between;
}
.root .content {
  text-align: center;
  padding: 20px;
}
.root .content h5 {
  color: #d58e32;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 1px;
}
.root .content .stars a {
  text-decoration: none;
  color: rgb(42, 42, 42);
}
.root .line {
  margin: 0 10px;
  border-bottom: 1px solid #2a2a2a;
  position: relative;
}
.root .line::before {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 50%;
  width: 30px;
  border-bottom: 4px solid #d58e32;
  transform: translateX(-50%);
}
.root .actions {
  padding: 15px 10px 10px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.root .favorite {
  background-color: #d58e32;
  color: #ffffff;
}
.root:hover .priceBtn {
  background-color: #d58e32;
}

@media screen and (max-width: 768px) {
  .root .photo .buttons {
    opacity: 1;
  }
}