/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.registerBox {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 20px auto;
}
.registerBox .switchContainer {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-right: 0;
}
.registerBox .error {
  display: block;
  color: red;
}
.registerBox .switchInput {
  display: none;
}
.registerBox .switchButton {
  display: block;
  position: absolute;
  cursor: pointer;
  width: 35px;
  height: 18px;
  background-color: transparent;
  border-radius: 30px;
  transition: background-color 0.3s;
  border: solid 1px rgb(169, 169, 169);
  margin-top: 2px;
}
.registerBox .switchCircle {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #363636;
  top: 6px;
  left: 4px;
  transition: transform 0.3s;
}
.registerBox .lable {
  color: rgb(169, 169, 169);
  margin-left: 45px;
}
.registerBox .orange {
  color: #d58e32;
}
.registerBox .conditions {
  font-size: 12px;
  color: #434343;
}
.registerBox .linkRegister {
  background-color: orange;
  border: none;
  border-radius: 3px;
  width: 150px;
  height: 40px;
  font-size: 14px;
  text-align: center;
  margin-left: 55%;
  color: #ffffff;
}
.registerBox .btn {
  color: #ffffff;
}
.registerBox .linkReturn {
  margin-left: 22px;
}
.registerBox .linkReturn a {
  display: flex;
  align-items: center;
}
.registerBox .fa {
  color: #d58e32;
  padding-right: 3px;
}
.registerBox .submitBtn {
  display: flex;
  align-items: center;
}