/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root .panelBar {
  margin: 30px 0;
  position: relative;
}
.root .panelBar :global(.row) > * {
  border-bottom: 4px solid #e2e2e2;
}
.root .panelBar .heading {
  position: absolute;
  top: 9px;
  left: 0;
  z-index: 2;
}
.root .panelBar .heading h3 {
  color: #d58e32;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 38px;
  margin: 0;
  letter-spacing: 1px;
}
.root .panelBar .heading::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 45px;
  border-bottom: 4px solid #d58e32;
}
.root .panelBar .menu {
  text-align: center;
  margin-top: 20px;
}
.root .panelBar .menu ul {
  display: flex;
  justify-content: flex-end;
  margin: 0;
  margin-right: 10px;
  padding: 0;
  list-style: none;
}
.root .panelBar .menu ul li {
  display: inline-block;
  margin: 0 0.4rem;
  font-weight: 600;
}
.root .panelBar .menu ul li:last-of-type {
  margin-right: 0;
}
.root .panelBar .menu ul li:first-of-type {
  margin-left: 0;
}
.root .panelBar .menu ul li a {
  color: rgb(42, 42, 42);
  position: relative;
  text-decoration: none;
  font-size: 18px;
  display: block;
  cursor: pointer;
}
.root .panelBar .menu ul li a.active::before, .root .panelBar .menu ul li a:hover::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  border-bottom: 4px solid #d58e32;
}
.root .panelBar .dots {
  margin: 0 auto 20px;
}
.root .panelBar .dots ul {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
  transform: translateY(15px);
}
.root .panelBar .dots ul li {
  display: inline-block;
  margin-left: 0.5rem;
}
.root .panelBar .dots ul li a {
  display: block;
  width: 13px;
  height: 13px;
  border-radius: 6px;
  background-color: #e1e1e1;
  font-size: 0;
}
.root .panelBar .dots ul li a.active, .root .panelBar .dots ul li a:hover {
  background-color: #d58e32;
}

.productsContainer {
  display: flex;
  width: 100%;
  justify-content: center;
  margin: auto;
}

.fadeOut {
  opacity: 0;
}

.fadeIn {
  opacity: 1;
}

.desktopProduct {
  width: calc(100% - 1rem);
}

.compare {
  position: sticky;
  bottom: 0;
  padding: 10px;
}

:export {
  animationTime: 250ms;
}

@media (max-width: 576px) {
  .root .panelBar .heading {
    position: relative;
    margin: 0 auto 20px;
  }
  .root .panelBar .menu ul {
    justify-content: center;
  }
}