$base-sizing: 1rem;

.container {
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 1fr 1fr;
}
@media (max-width: 576px) {
  .container {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 768px) and (max-width: 992px) {
  .container {
    grid-template-columns: 1fr 1fr;
  }
}
