/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  padding: 0 0 24px;
}
.root .photo {
  position: relative;
  padding: 10px 10px 0 10px;
  background-color: #f4f5f7;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  align-items: flex-end;
}
.root .photo img {
  object-fit: cover;
  width: 100%;
  height: 400px;
  padding: 0;
}
.root .slider {
  width: 100%;
  display: flex;
  margin-top: 10px;
}
.root .slider .item {
  width: 100%;
  height: 93px;
}
.root .slider .item img {
  margin: 0 5px;
  object-fit: cover;
  width: 90%;
  height: 91px;
}
.root .slider button {
  width: 33px;
  height: 92px;
  background-color: rgba(0, 0, 0, 0.5);
}
.root .slider button .fa {
  color: #ffffff;
}
.root .stars {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
}
.root .stars .emptyStar {
  color: rgba(0, 0, 0, 0.5);
}
.root .stars a {
  text-decoration: none;
  color: rgb(42, 42, 42);
  cursor: default;
}
.root .line {
  margin: 10px 0;
  border-bottom: 2px solid rgba(78, 78, 78, 0.4745098039);
  position: relative;
}
.root .buttonsSlider {
  display: flex;
  align-items: center;
}
.root .buttonsSlider a {
  background-color: #ffffff;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.root .price {
  margin: 20px 0;
  font-weight: bold;
  font-size: 22px;
}
.root .price .oldPrice {
  text-decoration: line-through;
  margin-right: 5px;
  color: rgba(0, 0, 0, 0.3);
}
.root .price .priceBox {
  color: #ffffff;
  background-color: black;
  padding: 1px 5px;
}
.root .actionBtns {
  margin: 20px 0 10px;
}
.root .actionBtns a {
  border-color: rgba(0, 0, 0, 0.3);
  font-weight: bold;
  margin-right: 5px;
}
.root .actionBtns a:hover {
  background-color: #d58e32;
  text-decoration: none;
  border: none;
}
.root .actionBtns .amount {
  margin-top: 20px;
}
.root .actionBtns .amount .amountTxt {
  display: inline-block;
  font-size: 14px;
}
.root .actionBtns .amount .amountInput {
  text-align: center;
  height: 100%;
  margin-left: 40px;
  margin-right: 5px;
  width: 60px;
  cursor: default;
}
.root .actionBtns .amount .amountInput:hover {
  background-color: #ffffff;
  color: black;
  border: 1px solid rgba(0, 0, 0, 0.3);
}
.root .overview h5 {
  font-size: 16px;
}
.root .overview p {
  font-size: 12px;
}
.root .infos p {
  justify-content: flex-start;
  margin-bottom: 5px;
  font-size: 14px;
}
.root .infos p span {
  display: inline-block;
  font-weight: 600;
  width: 120px;
}
.root .socials a {
  margin-right: 5px;
}
.root .socials a {
  text-decoration: none;
}
.root .socials a:first-child:hover {
  background-color: #4867aa;
  border: none;
}
.root .socials a:nth-child(2):hover {
  background-color: #c74337;
  border: none;
}
.root .socials a:nth-child(3):hover {
  background-color: #1da1f2;
  border: none;
}
.root .socials a:nth-child(4):hover {
  background-color: #c51e23;
  border: none;
}
.root .socials a:last-child:hover {
  background-color: #0077b5;
  border: none;
}

@media screen and (max-width: 576px) {
  .root .socials {
    display: flex;
    justify-content: space-between;
  }
  .root .socials a {
    margin-right: 0;
    text-align: center;
    padding: 5px 5px;
  }
}