/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  background-color: #434343;
}
.root ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.root ul li {
  display: inline-block;
  line-height: 55px;
}
.root ul li a.login::after {
  content: "Login";
}
.root ul li a.register::after {
  content: "Register";
}
.root ul li a {
  color: #ffffff;
  font-size: 13px;
}
.root ul li a:hover {
  color: #d58e32;
  text-decoration: none;
}
.root ul li .btn {
  color: #ffffff;
  font-size: 13px;
  background: transparent;
  border: none;
}
.root ul li .btn:hover {
  color: #d58e32;
  text-decoration: none;
}
.root .topOptions ul li {
  margin-right: 1.5rem;
}
.root .topOptions ul li .icon {
  margin-left: 5px;
}
.root .topMenu ul li {
  margin-left: 1.5rem;
}
.root .topMenu ul li .icon {
  margin-right: 5px;
}

@media (max-width: 576px) {
  .root ul li button span {
    display: none;
  }
  .root ul li a.login::after {
    content: "";
  }
  .root ul li a.register::after {
    content: "";
  }
  .root .topOptions {
    flex: 0 0 56%;
  }
  .root .topOptions ul li {
    margin-right: 0.6rem;
  }
}