/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.cart {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}
.cart .code {
  border: 0;
  margin: 20px auto;
  display: flex;
  align-items: center;
  padding: 0;
}
.cart .code input {
  padding: 10px 0 10px 10px;
  width: 50%;
  font-size: 14px;
  color: #2a2a2a;
  font-weight: 300;
  border: 1px solid rgb(169, 169, 169);
}
.cart .code button {
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1.2;
  height: 50px;
  padding: 11px 10px 11px 10px;
  width: 150px;
  font-size: 14px;
  cursor: pointer;
  font-weight: 300;
  margin-left: 30px;
  border: 0;
  background-color: #d58e32;
  color: #ffffff;
}
.cart .shoppingCart {
  width: 100%;
  margin: 80px auto;
  display: flex;
  flex-direction: column;
}
.cart .shoppingCart img {
  width: 100px;
}
.cart .shoppingCart .title {
  height: 60px;
  padding: 10px 30px;
  color: #2a2a2a;
  font-size: 18px;
  font-weight: 400;
  border-bottom: 1px solid rgb(169, 169, 169);
  border-right: 1px solid rgb(169, 169, 169);
  border-left: 1px solid rgb(169, 169, 169);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cart .shoppingCart .itemTitle {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgb(169, 169, 169);
  background-color: #f4f5f7;
  padding: 0 30px;
  margin: 0 auto;
  width: 100%;
}
.cart .shoppingCart .itemTitle .title {
  border: 0;
  width: 115px;
  margin: auto;
  display: flex;
  justify-content: center;
  padding: 0;
}
.cart .shoppingCart .itemTitle .title span {
  font-size: 16px;
}
.cart .shoppingCart .itemTitle .titleProduct {
  border: 0;
  width: 240px;
  margin: auto 0;
  text-align: left;
}
.cart .shoppingCart .itemTitle .titleProduct span {
  font-size: 16px;
}
.cart .shoppingCart button {
  width: 28px;
  font-size: 14px;
  border: 1px solid rgb(169, 169, 169);
  cursor: pointer;
  font-weight: 300;
  text-align: center;
}
.cart .shoppingCart .icon {
  font-size: 10px;
}
.cart .subTable {
  height: 205px;
  width: 400px;
  color: #2a2a2a;
  font-size: 18px;
  font-weight: 400;
  border: 1px solid rgb(169, 169, 169);
  display: flex;
  flex-direction: column;
  margin: 0px 350px 50px 0;
}
.cart .subTable .subTitle {
  background-color: #f4f5f7;
  border-bottom: 1px solid rgb(169, 169, 169);
  height: 40px;
}
.cart .subTable .subTitle h6 {
  text-align: center;
  padding-top: 10px;
}
.cart .subTable .subItem {
  border-bottom: 1px solid rgb(169, 169, 169);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
}
.cart .subTable .subItem .subBorder {
  border-right: 1px solid rgb(169, 169, 169);
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
}
.cart .subTable .subItem .subBorder span {
  margin: auto 0;
}
.cart .subTable button {
  padding: 11px 10px 11px 10px;
  text-align: center;
  width: 90%;
  font-size: 14px;
  cursor: pointer;
  font-weight: 300;
  margin: 20px;
  border: 0;
  background-color: #d58e32;
  color: #ffffff;
}
.cart .price {
  width: 115px;
  margin: auto;
  text-align: center;
  font-size: 16px;
  color: green;
  font-weight: 600;
}

@media (max-width: 992px) {
  .cart {
    flex-direction: column;
    align-items: center;
  }
  .cart .shoppingCart {
    width: 100%;
    margin: 20px auto;
  }
  .cart .shoppingCart .itemTitle {
    padding-right: 3px;
  }
  .cart .shoppingCart .itemTitle .title {
    font-size: 12px;
  }
  .cart .code input {
    padding: 10px 0 10px 5px;
  }
  .cart .code button {
    width: 80px;
  }
  .cart .subTable {
    width: 100%;
    margin: 20px auto;
  }
}