/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.rightBox {
  width: 100%;
  position: relative;
  margin-bottom: 20px;
}
.rightBox img {
  width: 100%;
  height: 190px;
  object-fit: cover;
}
.rightBox .rightContent {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
.rightBox .rightContent .titleContainer {
  text-shadow: 1px 1px 1px rgb(0, 0, 0);
  margin-right: 10px;
}
.rightBox .rightContent .titleContainer h2 {
  font-size: 26px;
  color: #d58e32;
}
.rightBox .rightContent .titleContainer h2 span {
  font-weight: 300;
}
.rightBox .rightContent .titleContainer h3 {
  font-size: 18px;
  letter-spacing: 5px;
  font-weight: 500;
  line-height: 0.2;
  margin-bottom: 10px;
}
.rightBox .rightContent .description {
  font-size: 18px;
}