/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.container {
  color: rgba(67, 67, 67, 0.75);
  margin: 0.5rem;
  border: 2px solid rgba(67, 67, 67, 0.2);
}
.container p {
  font-size: 14px;
  margin: 0;
  font-weight: 600;
  color: rgba(67, 67, 67, 0.75);
}
.container ul {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.container ul li {
  padding: 0.75rem 1.5rem;
  border: 2px solid rgba(67, 67, 67, 0.2);
  width: 25%;
  border-top: none;
  border-left: none;
  text-transform: uppercase;
  font-weight: 700;
}
.container ul li:last-of-type {
  border-right: none;
}
.container ul li.selected {
  border-bottom: none;
  color: #d58e32;
}
.container h5 {
  font-size: 15.4px;
  font-weight: 700;
  margin: 1.5rem 0;
}
.container .starsContainer {
  display: flex;
  gap: 1rem;
  margin: 1rem 0;
}
.container textarea {
  width: 100%;
  resize: none;
  height: 5rem;
  margin: 0.5rem 0;
  border: 2px solid rgba(67, 67, 67, 0.2);
}
.container .inputsContainer {
  width: 100%;
  display: flex;
  gap: 0.5rem;
}
.container .inputsContainer input {
  border: 2px solid rgba(67, 67, 67, 0.2);
  padding: 0.5rem;
  width: 100%;
}
.container .inputsContainer button {
  padding: 0 1.5rem;
  color: white;
  background-color: #434343;
}
.container .contents {
  padding: 2rem;
}

@media screen and (max-width: 576px) {
  .container {
    margin: 0.5rem 0;
  }
  .container ul li {
    padding: 0.75rem 0.5rem;
    font-size: 11px;
  }
}