/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.root {
  padding: 20px;
}
.root h5 {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.root ul {
  list-style: none;
  padding: 0;
}
.root ul li {
  margin: 5px 0;
  font-size: 14px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.root .post {
  margin: 20px 0 20px;
  width: 100%;
}
.root .post .title {
  text-transform: uppercase;
  margin-bottom: 15px;
}
.root .post img {
  width: 100%;
  object-fit: cover;
}
.root .post .description {
  font-size: 12px;
  margin: 15px 0 30px;
}
.root .post .line {
  border-bottom: 1px solid rgba(42, 42, 42, 0.4588235294);
  position: relative;
}
.root .post .footer {
  display: flex;
  justify-content: space-between;
  margin: 0 10px;
}
.root .post .footer .informations {
  display: flex;
  color: rgba(0, 0, 0, 0.5);
}
.root .post .footer .informations .admin,
.root .post .footer .informations .date,
.root .post .footer .informations .file {
  margin-right: 15px;
}
.root .post .footer .informations .admin p,
.root .post .footer .informations .date p,
.root .post .footer .informations .file p {
  display: inline-block;
  margin: 0;
  margin-left: 3px;
}
.root .post .footer .btn {
  border: none;
  background-color: #fff;
  color: rgba(0, 0, 0, 0.5);
}
.root .post .footer .btn:hover {
  font-weight: bold;
}
.root .menu {
  margin: 20px 0 20px;
}
.root .menu input {
  padding-left: 10px;
  outline: none;
}
.root .menu .recentPosts,
.root .menu .recentComments,
.root .menu .archives,
.root .menu .categories,
.root .menu .meta {
  margin: 40px 0;
}

@media (max-width: 992px) {
  .root .post .footer {
    font-size: 14px;
    text-align: center;
    margin: 0;
  }
  .root .post .footer .informations {
    align-items: center;
  }
  .root .post .footer .informations .admin,
  .root .post .footer .informations .date,
  .root .post .footer .informations .file {
    margin-right: 10px;
  }
  .root .post .footer .informations button {
    padding: 1px 0;
  }
}