/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.banner {
  background-color: #3f3f3f;
  padding: 3.5rem;
  color: #ffffff;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.banner .bannerTitle {
  font-size: 56px;
  font-weight: 300;
  width: 630px;
  letter-spacing: 2px;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
.banner .bannerSubtitle {
  font-weight: 300;
  width: 630px;
  letter-spacing: 12px;
  display: flex;
  justify-content: space-between;
  margin-left: 12px;
}
.banner .bannerSubtitle .subtitleColor {
  color: #d58e32;
  font-weight: bold;
}

.srcLinks {
  padding: 24px;
}
.srcLinks a {
  padding: 3px 6px;
  margin: 0 10px;
  text-decoration: none;
  color: #2a2a2a;
}
.srcLinks a.active {
  color: #d58e32;
}

@media (max-width: 576px) {
  .container {
    padding: 0;
  }
  .banner {
    padding: 2.5rem;
  }
  .banner .bannerTitle {
    max-width: 300px;
    font-size: 24px;
  }
  .banner .bannerSubtitle {
    margin-top: 6px;
    max-width: 300px;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .srcLinks {
    padding: 6px;
  }
  .srcLinks a {
    font-size: 14px;
  }
}