@keyframes fade-in {
  0% {
    filter: opacity(0);
  }
  100% {
    filter: opacity(1);
  }
}
@keyframes fade-out {
  0% {
    filter: opacity(1);
  }
  100% {
    filter: opacity(0);
  }
}
@keyframes scale-in {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.swipeLeft {
  overflow: hidden;
  transform: translateX(-3px);
}

.swipeRight {
  overflow: hidden;
  transform: translateX(3px);
}

.scaleIn {
  animation: scale-in 250ms, fade-in 250ms;
}