/* NOT FOR USE OUTSIDE THIS FILE */
.extra-hover {
  transform: translate(10px, 0) scale(1);
}

.item {
  padding: 20px 30px;
  height: 120px;
  display: flex;
  border-right: 1px solid rgb(169, 169, 169);
  border-left: 1px solid rgb(169, 169, 169);
  border-bottom: 1px solid rgb(169, 169, 169);
}
.item .buttons {
  margin: auto;
  padding: 0;
  width: 115px;
  text-align: center;
}
.item .buttons .deleteBtn {
  cursor: pointer;
  font-size: 2em;
}
.item .image {
  margin-right: 50px;
  width: 100px;
}
.item .description {
  width: 200px;
  margin: auto;
  text-align: left;
  padding-left: 5px;
}
.item .price {
  width: 115px;
  margin: auto;
  text-align: center;
  font-size: 16px;
  color: green;
  font-weight: 600;
}
.item .quantity {
  margin: auto;
  width: 115px;
  text-align: center;
}
.item .quantity input {
  text-align: center;
  width: 28px;
  font-size: 14px;
  color: #2a2a2a;
  font-weight: 900;
  border: none;
  margin: 0 2px;
  outline: none;
}
.item .quantity button {
  position: relative;
  border-radius: 25%;
  background-color: #d58e32;
  border: none;
}
.item .quantity button:hover {
  background-color: #dea55c;
}
.item .quantity button:active {
  top: 2px;
}
.item .quantity .icon {
  font-size: 10px;
}
.item .totalPrice {
  width: 115px;
  margin: auto;
  text-align: center;
  font-size: 16px;
  color: green;
  font-weight: 600;
}

@media (max-width: 992px) {
  .item {
    padding: 20px 0;
  }
  .item .buttons {
    width: 70px;
  }
  .item .buttons .deleteBtn {
    font-size: 1em;
  }
  .item .image {
    margin-right: 0;
  }
  .item .quantity {
    margin: auto;
    width: 115px;
    text-align: center;
  }
  .item .quantity input {
    height: 30px;
    margin: 0 2px;
  }
}